.ui-widget {
    font-family: inherit;
}
.ui-datepicker {
    width: auto;
    padding: 10px 20px 20px;
    background-color: #fff;
    box-shadow: 1px 3px 5px 1px rgba(0, 0, 0, 0.4);
    th {
        opacity: 0.5;
        border-bottom: 1px solid #d5e9f7;
        font-weight: 400;
    }
    td a,
    td span {
        width: inherit;
        height: inherit;
        margin: -15px;
        @include flex-center;
    }
    &-calendar {
        border-spacing: 0;
    }
}

.ui-datepicker-next-hover,
.ui-datepicker-prev-hover {
    position: relative;
    &:after {
        content: "";
        width: 125%;
        height: 125%;
        @include abs-center;
        background-color: #d5e9f7;
    }
}
.ui-state-hover {
    font-weight: 700;
    cursor: pointer;
    position: relative;
    z-index: 5;
    &::after {
        content: "";
        width: 125%;
        height: 125%;
        background-color: #d5e9f7;
        border-radius: 50%;
        z-index: -1;
        @include abs-center;
    }
}
.ui-datepicker {
    font-size: 12px;
    line-height: math.div(15, 12);
    &-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 10px;
    }
    &-prev,
    &-next {
        position: relative;
        width: 20px;
        height: 20px;
        padding: 0;
        span {
            width: 100%;
            height: 100%;
            display: block;
            top: 50%;
            left: 0;
            text-indent: 0;
            font-size: 0;
            line-height: 0;
            margin: 0;
            &::after {
                content: "\e902";
                font-family: icons;
                position: absolute;
                top: 0;
                text-align: center;
                width: 100%;
                height: 100%;
                font-size: 20px;
                line-height: 1;
                color: #389ae0;
            }
        }
    }
    &-prev {
        order: 1;
        span {
            &::after {
                left: 0;
                transform: rotate(90deg);
            }
        }
    }
    &-next {
        order: 3;
        span {
            &::after {
                right: 0;
                transform: rotate(-90deg);
            }
        }
    }
    &-title {
        font-size: 22px;
        line-height: math.div(39, 30);
        font-weight: 600;
        color: #1d3456;
        margin: 0;
        text-align: center;
        order: 2;
    }
    &-year {
        display: none;
    }

    td {
        padding: 15px;
        width: 30px;
        height: 30px;
        &:nth-last-child(2) {
            color: #389ae0;
        }
        &:last-child {
            color: #ff831e;
        }
    }
    .date-range-selected {
        background-color: #d5e9f7;
        .ui-state-hover {
            &::after {
                width: 100%;
                height: 100%;
            }
        }
    }

    .date-range-selected-start,
    .date-range-selected-end,
    &-current-day,
    &-today {
        position: relative;
        z-index: 5;
        &:after {
            content: "";
            width: 125%;
            height: 125%;
            border-radius: 50%;
            @include abs-center;
            z-index: -1;
        }
    }
    .date-range-selected-start,
    .date-range-selected-end,
    &-current-day {
        color: #fff;
        &:after {
            background-color: #389ae0;
        }
        .ui-state-hover {
            &:after {
                background-color: transparent;
            }
        }
    }
    &-today {
        &:after {
            background-color: #eff6fc;
        }
    }
}

.ui-state-disabled {
    opacity: 0.6;
    cursor: not-allowed;
}

.ui-datepicker td.date-range-selected-start-end > .ui-state-active,
.ui-datepicker td.date-range-selected-start-end > .ui-state-default {
    color: #fff;
    position: static;
    &::after {
        content: "";
        position: absolute;
        width: 50%;
        height: 100%;
        top: 0;
        z-index: -1;
        border-radius: 0;
        background-color: #d5e9f7;
        transform: none;
    }
}
.ui-datepicker td.date-range-selected-start > .ui-state-active,
.ui-datepicker td.date-range-selected-start > .ui-state-default {
    &:after {
        left: 50%;
    }
}
.ui-datepicker td.date-range-selected-end > .ui-state-active,
.ui-datepicker td.date-range-selected-end > .ui-state-default {
    &:after {
        left: 0;
    }
}
.ui-datepicker-trigger {
    position: absolute;
    top: 50%;
    right: 18px;
    width: 18px;
    height: 18px;
    transform: translateY(-50%);
    padding: 0 !important;
    color: #389ae0;
    &::after {
        content: "\e901";
        font-family: icons;
        width: 100%;
        height: 100%;
        line-height: 1;
        font-size: 18px;
        position: absolute;
        top: 0;
        left: 0;
    }
    &:focus {
        outline: 2px solid;
        outline-offset: 3px;
        border-radius: 1px;
    }
}
