.page {
    &__miami-realty-advantages {
        @include avalue("padding-top", 50, 40);
        @include avalue("padding-top", 50, 20);
    }
}
.miami-realty-advantages {
    &__container {
        @media (max-width: 375px) {
            padding-left: 14px;
            padding-right: 14px;
        }
    }
    &__header {
        @include avalue("margin-bottom", 40, 10);
        br.responsive {
            @media (max-width: 900px) {
                display: inline;
            }
        }
    }
    &__title {
    }
    &__subtitle {
        @include afont(25, 16);
        line-height: math.div(32, 25);
        text-align: center;
    }
    &__list {
        margin: -35px 0;
        flex-wrap: wrap;
    }
    .slider-controls {
        display: none;
    }
    @media (max-width: 375px) {
        .slider-controls {
            display: block;
        }
        &__list {
            margin: 0;
        }
        &__list-container {
            margin-bottom: 20px;
            overflow: hidden;
            @media (max-width: 375px) {
                margin-right: -14px;
            }
        }
        &__list-item {
            flex: 0 0 190px;
            padding: 0px 0;
            &:not(:last-child) {
                // margin-right: 14px;
            }
        }
        &__text {
            padding: 0px;
        }
    }
}
