.search {
    // padding: 25px 30px 70px;
    @include avalue("padding-top", 25, 20);
    @include avalue("padding-bottom", 70, 30);
    @include avalue("padding-left", 50, 25);
    @include avalue("padding-right", 50, 25);
    background-color: #fff;
    &__container {
    }
    &__inner {
        width: 98%;
    }
    &__body {
        @include avalue("padding", 30, 14);
    }
    &__header {
        margin-bottom: 45px;
        height: 10px;
        justify-content: flex-end;
    }
    &__input {
    }
    &__input-before {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 10px;
        padding: 0 !important;
        @include fon(20, 20);
    }
    &__options {
        display: flex;
        align-items: center;
    }
    &__clear-input {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 10px;
        padding: 0 !important;
        font-size: 0;
        line-height: 0;
        margin: 0;
        padding: 0;
        &::before {
            @include fon(20, 20);
        }
    }
    &__form {
        display: flex;
        align-items: flex-start;
    }
    &__input {
        flex-grow: 1;
        input {
            width: 100%;
            margin-bottom: 0;
            padding: 9px 50px;
            border-radius: 0;
            border-width: 0;
            border-bottom: 1px solid rgba(0, 0, 0, 0.3);
            &::placeholder {
                opacity: 0.5;
                font-style: normal;
                max-width: 1000px;
                line-height: inherit;
            }
        }
    }
    &__input-wrapper {
        position: relative;
        // flex: 0 1 1000px;
        margin-right: 10px;
    }

    &__main-col {
        width: 100%;
    }
    &__form-fields {
        display: flex;
        align-items: start;
        @media (max-width: 700px) {
            flex-wrap: wrap;
        }
    }
    &__error-container {
        display: flex;
        flex-direction: column;
        min-height: 2em;
    }
    .choices__list--dropdown .choices__item--selectable,
    .choices__list[aria-expanded] .choices__item--selectable {
        padding-right: 10px;
    }
    .choices__list--single {
        padding-right: 25px;
    }
    .search__select-label {
        opacity: 0.5;
        margin: 0 20px 0 0;
        padding: 0;
        font-weight: 400;
    }
    &__hints {
        max-width: 1000px;
    }
    &__hints-text {
        margin-bottom: 15px;
        opacity: 0.5;
    }
    &__hints-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        margin-top: -20px;
        flex: 1 0 1000px;
    }
    &__hint {
        border: 1px solid rgba(7, 39, 62, 0.5);
        border-radius: 5px;
        @include avalue("padding-top", 13, 10);
        @include avalue("padding-bottom", 13, 10);
        @include avalue("padding-left", 15, 10);
        @include avalue("padding-right", 15, 10);
        @include avalue("margin-top", 20, 14);
        white-space: nowrap;
        cursor: pointer;
        &:not(:last-child) {
            @include avalue("margin-right", 20, 14);
        }
    }
    &__submit-button {
        margin: 15px auto 15px;
        @include avalue("margin-top", 25, 15);
        width: 100%;
    }
    .choices {
        font-size: inherit;
    }
}
