.useful {
    // .useful__container
    &__container {
        @media (max-width: 768px) {
            padding: 0 24px;
        }
        @media (max-width: 680px) {
            padding: 0;
        }
    }
    &__header {
    }
    // .useful__title
    &__title {
        @media (max-width: 680px) {
            margin-left: 14px;
        }
    }
    // .useful__title-link
    &__title-link {
        @media (max-width: 768px) {
            display: none;
        }
    }
    &__low-link {
        display: none;
        @media (max-width: 768px) {
            display: block;
            margin-top: 30px;
        }
        @media (max-width: 680px) {
            margin-right: 24px;
        }
        @media (max-width: 375px) {
            margin-right: 14px;
        }
    }
    &__articles {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: -30px;
        padding-bottom: 30px;
        margin-left: -20px;
        margin-right: -20px;
        @include avalue("margin-left", -20, 0);
        @include avalue("margin-right", -20, 0);
    }
    // .useful__article
    &__article {
        flex: 0 0 33.3333%;
        @media (max-width: 768px) {
            flex: 0 0 50%;
        }
        @media (max-width: 450px) {
            flex: 0 0 100%;
        }
        @include avalue("margin-top", 20, 15);
        padding-top: 0;
        padding-bottom: 0;
        @include avalue("padding-left", 20, 0);
        @include avalue("padding-right", 20, 0);
        @media (max-width: 768px) {
            // padding: 0 12px;
        }
        &:nth-child(1) {
            height: 430px;
        }
        &:nth-child(2) {
            height: 318px;
        }
        &:nth-child(3) {
            height: 475px;
            margin-right: 0;
        }
        &:nth-child(4) {
            height: 180px;
            margin-top: -15px;
        }
        &:nth-child(5) {
            height: 310px;
            margin-top: -143px;
        }
        &:nth-child(6) {
            height: 135px;
            margin-right: 0;
            margin-top: 30px;
        }
        @media (max-width: 768px) {
            &:nth-child(1) {
                height: 370px;
            }
            &:nth-child(2) {
                height: 300px;
            }
            &:nth-child(3) {
                height: 400px;
                margin-right: 0;
            }
            &:nth-child(4) {
                height: 260px;
                margin-top: -40px;
            }
            &:nth-child(5) {
                height: 356px;
                margin-top: -190px;
                order: 6;
            }
            &:nth-child(6) {
                height: 135px;
                margin-right: 0;
                order: 5;
            }
        }
        @media (max-width: 450px) {
            margin-top: 20px !important;
            max-height: 150px;
        }
    }
}

.article {
    color: #fff;
    &__body {
        position: relative;
        height: 100%;
        background-color: #222222;
    }
    &__content-container {
        padding: 20px 15px;
        @include abs;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        overflow: hidden;
        transition: background-color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                cursor: pointer;
            }
        }
    }
    &__content {
        overflow: hidden;
        transition: transform 0.3s;
    }
    // .article__image
    &__image {
        vertical-align: top;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    // .article__title
    &__title {
        @include fon(20, 30, 600);
        margin-bottom: 1em;
        a {
            display: block;
        }
    }
    // .article__text-preview
    &__text-preview {
        display: block;
        text-overflow: ellipsis;
        max-height: 4.6em;
        overflow: hidden;
    }
}
