.first-screen {
    background-color: #5f91a7;
    margin-top: 90px;
    color: #fff;
    position: relative;
    .bg {
        z-index: 0;
    }
    // .first-screen__container
    &__container {
        position: relative;
        z-index: 5;
        // @include avalue("height", 690, 280);
        padding-top: 40px;
        padding-left: 20px;
        @media (min-width: 1440px) {
            padding-top: 200px;
            padding-left: 140px;
        }
        @media (min-width: 1280.02px) {
            padding-top: 170px;
            padding-left: 130px;
        }
        @media (min-width: 992.02px) {
            padding-top: 130px;
            padding-left: 100px;
        }
        @media (min-width: 768.02px) {
            padding-top: 100px;
            padding-left: 70px;
        }
        @media (min-width: 440.02px) {
            padding-top: 90px;
            padding-left: 50px;
        }
        @media (min-width: 320.02px) {
            padding-top: 80px;
            padding-left: 40px;

        }
        // @include avalue("padding-top", 177, 45);
        // @include avalue("padding-left", 143, 15);
    }
    // .first-screen__title
    &__title {
        // @include fon(60, 73, 800);
        letter-spacing: 0.04em;
        margin-bottom: 25px;
        font-weight: 800;
        line-height: math.div(73, 60);
        @include avalue("font-size", 60, 25);
    }
    // .first-screen__subtitle
    &__subtitle {
        @include afont(30, 16);
        font-weight: 600;
        line-height: math.div(39, 30);
        @include avalue("margin-bottom", 40, 18);
    }
    // .first-screen__button
    &__plan-trip-button {
        display: flex;
        align-items: baseline;
        position: relative;
        // padding-right: 73px !important;
        // @include avalue("padding-right", 73, 15);
        @include avalue("padding-top", 14, 10);
        @include avalue("padding-bottom", 14, 10);
        @include avalue("width", 240, 210);
        &::before,
        &::after {
            position: absolute;
            color: inherit;
            top: 50%;
        }
        &::before {
            content: ""; // tarmac
            right: 19px + 16px - 2px;
            width: 30px;
            height: 1px;
            transform: translateY(-50%);
            font-size: 16px;
            background: no-repeat linear-gradient(to right, transparent, white, transparent);
        }
        &::after {
            content: "\e90d"; // plane
            font-family: icons;
            right: 19px;
            width: 16px;
            height: 16px;
            font-size: 16px;
            transform: translateY(-50%) rotate(90deg);
        }
        span {
            &.icon-dark-tarmac {
                &::before {
                    margin-left: 10px;
                    margin-right: -1px;
                }
            }
        }
    }
    // .first-screen__link
    &__breadcrumbs {
        display: flex;
        align-items: center;
        margin-top: auto;
        margin-top: 30px;
        padding-bottom: 15px;
        @media (min-width: 1280px) {
            padding-bottom: 30px;
            margin-top: 80px;
        }
        @media (min-width: 768px) {
            padding-bottom: 25px;
            margin-top: 70px;
        }
        @media (min-width: 440px) {
            padding-bottom: 20px;
            margin-top: 50px;
        }
        &-link {
            padding-right: 10px;
            position: relative;
            display: inline-block;
            font-weight: 500;
            font-size: 12px;
            line-height: math.div(15, 12);
            span {
                display: inline-block;
                font-size: 16px;
                width: 19px;
            }
            &:not(:first-child) {
                padding-left: 5px;
            }
            &:not(:last-child) {
                &::after {
                    content: "\e902";
                    font-family: icons;
                    position: absolute;
                    top: 50%;
                    right: 0;
                    width: 7px;
                    height: 7px;
                    font-size: 7px;
                    line-height: 1;
                    display: inline-block;
                    transform: rotate(-90deg) translateX(50%);
                }
            }
        }
    }
}
