.tab-button {
    position: relative;
    padding: 7px 0;
    &::before {
        content: "";
        position: absolute;
        bottom: -4px;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: rgba(189, 189, 189, 0.5);
    }
    &.button--active::before {
        height: 4px;
        background-color: #389ae0;
        transition: background-color 900ms, height 200ms linear;
    }
}
.tab-bar {
    display: flex;
    flex-wrap: wrap;
    @include avalue("margin-bottom", 34, 15);
    button {
        flex-grow: 1;
        opacity: 0.3;
        line-height: math.div(39, 20);
        font-weight: 600;
        @include afont(30, 16);
        text-align: center;
        transition: opacity 0.3s;
        @media (any-hover: hover) {
            &:hover {
                opacity: 1;
            }
        }
    }
    .button--active {
        opacity: 1;
    }
}
.tab {
    display: none;
    opacity: 0;
    transition: all 500ms;
    // .tab__inner-tab-bar
    &__inner-tab-bar {
        button {
            font-size: 20px;
            line-height: math.div(30, 20);
        }
    }
    // .tab__inner-tabs
    &__inner-tabs {
    }
}
.inner-tabs {
    @include transition("opacity");
    // .inner-tabs__inner-tab
    &__inner-tab {
    }
}
.inner-tab {
    // .inner-tab__title
    &__title {
        display: none;
    }
    // .inner-tab__body
    &__body {
        padding-top: 20px;
        // display: flex;
    }
    // .inner-tab__image
    &__image {
        max-width: 50%;
        max-height: 100%;
        @include avalue("margin-bottom", 30, 10);
    }

    &__image-right {
        float: right;
        @include avalue("margin-left", 30, 10);
    }
    &__image-right-then-center {
        @media (min-width: 768px) {
            float: right;
            @include avalue("margin-left", 30, 10);
        }
        @media (max-width: 768px) {
            width: 100%;
            max-width: 100% !important;
            margin-right: 0;
        }
    }
    &__image-left {
        float: left;
        @include avalue("margin-right", 30, 10);
    }
    &__image-left-then-center {
        @media (min-width: 768px) {
            float: left;
            @include avalue("margin-right", 30, 10);
        }
        @media (max-width: 768px) {
            width: 100%;
            max-width: 100% !important;
            margin-right: 0;
        }
    }

    &__image-center {
        max-width: 100%;
    }

    // .inner-tab__text
    &__text {
        opacity: 0.8;
        @include avalue("margin-bottom", 25, 10);
        p:not(:last-child) {
            @include avalue("margin-bottom", 20, 10);
        }
        button {
            margin-top: 20px;
            &:not(:last-child) {
                @include avalue("margin-right", 40, 20);
            }
        }
    }
    // .inner-tab__links
    &__links {
        margin-bottom: 37px;
    }
    &__link-list {
        display: flex;
        flex-wrap: wrap;
        margin-top: -10px;
    }
    &__link-item {
        margin-top: 10px;
        &:not(:last-child) {
            @include avalue("margin-right", 35, 10);
        }
    }
    &__link {
        font-weight: 600;
        text-decoration: underline;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: #389ae0;
            }
        }
    }
}
