    }
    &__button-group {
        display: flex;
    }
    &__button {
        &:not(:last-child) {
            margin-right: 40px;
        }
    }
    &__tab-bar-container {
    }
    &__tab-bar {
        margin-bottom: 0;
        display: flex;
        flex-wrap: wrap;
        button {
            width: auto;
            line-height: 1.5;
            @include afont(20, 14);
            white-space: nowrap;
            padding: 0 10px;
            span {
                position: relative;
                padding: 5px 0 9px;
                overflow: hidden;
                display: inline-block;
            }
            span::before {
                transition: transform 0.3s;
                content: "";
                position: absolute;
                bottom: 0;
                left: 0;
                background: #389ae0;
                height: 0px;
                width: 60px;
                transform: translateX(-100%);
            }
            &.button--active span::before {
                height: 4px;
                transform: translateX(0);
            }
        }
    }
}
.tab--active {
    display: block;
    opacity: 1;
}
.tab--is-fading-out {
    opacity: 0;
    display: block;
}
.tab--is-fading-in {
    display: block;
    opacity: 1;
}
.inner-tab__text-list {
    li {
        // margin-left: 40px;
        @include avalue("margin-left", 40, 20);
        margin-bottom: 15px;
    }
    li::before {
        content: "\2022";
        color: #389ae0;
        // width: 40px;
        @include avalue("width", 40, 20);
        height: 10px;
        font-size: 20px;
        font-weight: 900;
        // margin-left: -40px;
        @include avalue("margin-left", -40, -20);
        display: inline-block;
    }
}
