.contacts {
    @include flex-center;
    font-weight: 500;
    &-item {
        display: flex;
        justify-content: center;
        text-align: center;
        flex-direction: column;
        position: relative;
        border-image: linear-gradient(rgba(255, 255, 255, 0.06), #fff, rgba(255, 255, 255, 0.06)) 1;
        border-width: 0 1px 0 0;
        border-style: solid;
        padding-left: 40px;
        padding-right: 40px;
        @include avalue("padding-top", 20, 15);
        @include avalue("padding-bottom", 20, 15);
        // @media (max-width: 768px) {
        //     padding: 26px 26px;
        // }
        &:last-child {
            border-right: none;
        }
    }
    &-text {
        text-align: center;
        white-space: nowrap;
        @include afont(16, 12);
    }
    &__link {
        font-size: 0;
        line-height: 0;
        margin: 11px 0 0 0;
        display: block;
        &::before {
            font-size: 20px;
            line-height: 30px;
        }
        @media (any-hover: hover) {
            &:hover {
                text-shadow: 0 0 5px white;
            }
        }
    }

    &__call-link {
        @include afont(20, 14);

        font-weight: 600;
        margin: 11px 0 0 0;
        white-space: nowrap;
        @media (any-hover: hover) {
            &:hover {
                text-shadow: 0 0 3px #fff;
            }
        }
        &::before {
            vertical-align: middle;
            display: inline;
            margin-right: 5px;
        }
    }
    &-link-block {
        display: flex;
        justify-content: space-between;
    }
}
