    position: relative;
    text-shadow: 0px 0px 27px rgba(0, 0, 0, 0.05);
    @media (max-width: 768px) {
        background-size: 550%;
        background-position: 65% 80%;
        position: relative;
        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 0;
            pointer-events: none;
