    .swiper-slide-prev {
        &::after {
            opacity: 0;
        }
    }
    &__item {
        text-align: center;
    }
    &__item-title {
        @include amaxvalue("font-size", 30, 16, 768);
        line-height: 1;
        margin-bottom: 25px;
        font-weight: 600;
        text-overflow: ellipsis;
        overflow: hidden;
        @media (max-width: 768px) {
            margin-bottom: 20px;
        }
        @media (max-width: 375px) {
            line-height: math.div(19, 16);
            margin-bottom: 15px;
        }
    }

    &__trust-management {
        color: #fff;
        margin-top: 222px;
        display: flex;
        justify-content: space-between;
        text-shadow: 0px 0px 27px rgba(0, 0, 0, 0.05);
        @media (max-width: 768px) {
            flex-direction: column;
            text-align: center;
            margin-top: 75px;
        }
    }
    &__trust-management-header {
        flex-basis: 734px;
        @media (max-width: 768px) {
            flex-direction: column;
            flex-basis: auto;
            margin-bottom: 15px;
        }
    }
    &__trust-management-title {
        margin-bottom: 5px;
        text-align: left;
        @include amaxvalue("font-size", 50, 20, 768);
        @media (max-width: 768px) {
            text-align: center;
            margin-bottom: 15px;
        }
    }
    &__trust-management-subtitle {
        font-size: 30px;
        @include amaxvalue("font-size", 30, 16, 768);
        line-height: math.div(39, 30);
        font-weight: 600;
    }
    &__trust-management-about {
        flex-basis: 481px;
        font-size: 20px;
        @include amaxvalue("font-size", 20, 14, 768);
        line-height: 1.5;
        font-weight: 600;
        @media (max-width: 768px) {
            line-height: math.div(17, 14);
            flex-basis: auto;
        }
        p {
            &:not(:last-child) {
                margin-bottom: 20px;
                @media (max-width: 768px) {
                    margin-bottom: 15px;
                }
            }
        }
    }
    &__contract-button {
        margin-left: auto;
        margin-right: auto;
        display: block;
        @include amaxvalue("margin-top", 60, 20, 768);
    }
    .slider-controls {
        display: none;
    }
    @media (max-width: 375px) {
        .slider-controls {
            display: block;
        }
        &__list {
            margin: 0;
            margin-left: -14px;
            margin-bottom: 15px;
        }
        &__list-container {
            @media (max-width: 375px) {
                margin-right: -14px;
                padding-left: 14px;
            }
        }
        &__list-item {
            flex: 0 0 190px;
            width: 190px;
            padding: 14px 0;
            margin-right: 14px;
        }
        &__text {
            padding: 10px;
        }
    }
}
