.slider__pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 20px 30px;
    .slider__pagination-page-num {
        color: #333;
        @include fon(14, 21);
        display: block;
    }
    .slider__pagination-progress {
        width: 100%;
        max-width: 400px;
        height: 20px;
        position: relative;
        display: flex;
        align-items: center;
        margin: 0 12px;
        @include transition;
        .slider__pagination-bullet-trail {
            position: absolute;
            top: 50%;
            left: 0;
            right: 0;
            height: 4px;
            transform: translateY(-50%);
            background-color: rgba(141, 97, 97, 0.3);
        }
        .slider__pagination-bullet {
            // width: dinamic; in app.js
            height: 20px;
            position: relative;
            cursor: pointer;
            overflow: hidden;
            // &:hover {
            //     background: rgba(255, 131, 30, 0.15);
            //     border-radius: 5px;
            // }
            &::before {
                content: "";
                @include abs();
                margin: 8px 0;
                height: 4px;
                width: 100%;
                background: rgba(141, 97, 97, 0.3);
            }
            &::after {
                content: "\e90d";
                font-family: icons;
                @include size(16);
                position: absolute;
                transform: rotate(90deg);
                color: #ff831e;
                right: calc(100% - 5px);
                bottom: 1px;
                transition: all 0.3s;
                //  z-index: 10;
            }
            .slider__pagination-bullet-trail {
                background: #ff831e;
            }
            &-active {
                .slider__pagination-bullet-trail {
                    background: linear-gradient(
                        360 - 270.7deg,
                        #ff831e 0.2%,
                        rgb(255, 255, 255, 1) 100%
                    );
                    animation-name: trailForward;
                    animation-duration: 0.3s;
                    animation-timing-function: linear;
                }
                //  &:hover {
                //      background: rgba(255, 131, 30, 0.15);
                //  }
                &::after {
                    display: block;
                    right: -5px;
                    animation-name: plane;
                    animation-duration: 0.3s;
                    animation-timing-function: linear;
                }
                & ~ .slider__pagination-bullet {
                    //   &:hover {
                    //       background: rgba(141, 97, 97, 0.1);
                    //   }
                    &::before {
                        background: rgba(141, 97, 97, 0.3);
                    }
                    .slider__pagination-bullet-trail {
                        background: transparent;
                    }
                }
            }
        }
    }
}
@keyframes plane {
    0% {
        right: calc(100% - 16px);
    }
    100% {
        right: -5px;
    }
}
@keyframes trailForward {
    0% {
        width: 0;
    }
    100% {
        width: 100%;
    }
}
@keyframes trailBackward {
    0% {
        width: 100%;
    }
    100% {
        width: 0;
    }
}
.slider-pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    .slider-pagination__current-page {
        @include fon(30, 39, 600);
        margin-right: 20px;
        width: 39px;
        text-align: center;
    }
    .pagination-button {
        font-family: icons;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        border: 1px solid;
        position: relative;
        cursor: pointer;
        &::after {
            content: "\e902";
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
    .pagination-button-next {
        &::after {
            transform: translate(-50%, -50%) rotate(-90deg);
        }
    }
    .pagination-button-prev {
        margin-right: 24px;
        &::after {
            transform: translate(-50%, -50%) rotate(90deg);
        }
    }
}
.scrollbar {
    max-width: 300px;
    height: 4px;
    margin: 0 auto 30px;
    background: rgba(141, 97, 97, 0.3);
    @media (max-width: 320px) {
        margin-left: 30px;
        margin-right: 30px;
    }
    &__trail {
        height: 100%;
        background-color: #ff831e;
        transition: width 0.3s;
        display: flex;
        justify-content: flex-end;
    }
    &__trail-section {
        flex-shrink: 0;
        position: relative;
        background: linear-gradient(90deg, #ff831e, #fefefe);
        z-index: 10;
    }
    &__drag {
        font-size: 16px;
        position: absolute;
        color: #ff831e;
        right: 0;
        top: 50%;
        transform-origin: 50%;
        transform: rotate(90deg) translate(-50%, 0);
    }
}
