.client-reviews {
    // .client-reviews__container
    &__container {
        @media (max-width: 360px) {
            padding: 0;
        }
    }
    &__header {
        @media (max-width: 768px) {
            flex-direction: column;
        }
    }
    // .client-reviews__title
    &__title {
        text-align: left;
        @media (max-width: 768px) {
            text-align: center;
            margin-bottom: 10px;
        }
    }
    // .client-reviews__read-all
    &__read-all {
        @media (max-width: 768px) {
            align-self: flex-end;
        }
    }

    .slider {
        // .slider__container
        &__container {
        }
    }
    .swiper {
        transform-style: preserve-3d;
        margin: 0 auto;
        width: 620px;
        margin-bottom: 36px;
        overflow: visible;
        @media (max-width: 800px) {
            overflow: hidden;
            width: 100%;
        }
    }
    .swiper-wrapper {
        position: relative;
        align-items: center;
        width: 100%;
        z-index: 1;
        perspective: 1200px;
        // box-sizing: content-box;
        // transform-style: preserve-3d;
        overflow: visible;
        transition: all 300ms;
        // outline: 5px solid rgb(138, 43, 226);
        // background-color: rgba(138, 43, 226, 0.3);
    }
    .swiper-slide {
        flex-shrink: 0;
        backface-visibility: hidden;
        transition-property: transform, opacity, height;
    }
}
