.realty {
    &__important {
    }
}
.important {
    &__container {
        @include amaxvalue("padding-left", 24, 14, 768);
        @include amaxvalue("padding-right", 24, 14, 768);
    }
    &__header {
        @media (max-width: 1000px) {
            margin-bottom: 15px;
        }
    }
    &__body {
    }
    &__tabs {
    }
    &__tab-bar {
        @include amaxvalue("font-size", 20, 16, 1000);
        @include amaxvalue("margin-bottom", 25, 0, 1000);
        button {
            padding: 4px 15px;
        }
        @media (max-width: 1000px) {
            flex-direction: column;
            line-height: math.div(19, 16);
            .tab-button::after {
                bottom: 0;
            }
        }
        button {
            flex: auto;
            line-height: 1.5;
            width: auto !important;
            @media (max-width: 1000px) {
                padding-top: 10px;
                padding-bottom: 10px;
            }
        }
    }
    &__tab-button {
    }
}
