.popular {
    background-color: rgba(229, 229, 229, 0.2);
    &__container {
        @include amaxvalue("padding-left", 24, 12, 1024);
        @include amaxvalue("padding-right", 24, 12, 1024);
        @media (max-width: 1024px) {
            padding-left: 0;
            padding-right: 0;
        }
        display: flex;
        flex-direction: column;
        padding-bottom: 0;
        width: 100%;
    }
    &__body {
        display: flex;
        align-items: flex-start;
    }
    &__filter-button {
        @media (min-width: 950 + 1px) {
            display: none;
        }
        margin-bottom: 10px;
        margin-left: auto;
        margin-right: 24px;
        width: 110px;
        height: 40px;
        font-size: 12px;
        line-height: math.div(21, 14);
        padding: 10px;
        background-color: #fff;
        border-radius: 5px;
        &::before {
            content: "\e90f";
            font-family: icons;
            margin-right: 10px;
        }
        @media (max-width: 320px) {
            width: 100%;
        }
    }
    &__filter {
        flex: 0 0 25%;
        // margin-right: 40px;
        // @include avalue("margin-right", 40, 10);
        @include aminvalue("margin-right", 40, 10, 950);

        @media (max-width: 950px) {
            display: none;
        }
    }

    &__cards {
        flex-grow: 1;
        border-radius: 5px;
    }
    &__cards-list {
        display: flex;
        flex-direction: column;
        gap: 20px;
        &.loading {
            @include flex-center;
            min-height: 300px;
        }
    }
    &__cards-list-item {
        box-shadow: 0px 0px 27px rgba(0, 0, 0, 0.05);
        &:not(:last-child) {
            @include aminvalue("margin-bottom", 30, 15, 950);
            @include amaxvalue("margin-bottom", 30, 20, 950);
        }
    }

    &__card {
    }

    &__cards-catalog-link {
        align-self: flex-end;
        margin-top: 30px;
        @media (max-width: 1024px) {
            margin-right: 24px;
        }
    }
}

.card {
    width: 100%;
    background-color: #fff;
    padding: 30px;
    display: flex;
    gap: 20px 30px;
    .loading {
        @include size(26);
    }
    @media (max-width: 768px) {
        column-gap: 20px;
    }
    @media (max-width: 600px) {
        flex-direction: column;
    }
    @media (max-width: 400px) {
        padding: 25px 15px;
    }
    @media (max-width: 320px) {
        flex-direction: column;
        padding: 15px;
    }
    &__image {
        flex: 0 0 33%;
        @include flex-center;
        align-self: flex-start;
        overflow: hidden;
        .loading {
            @include size(66);
        }
        // width: 261px;
        //   height: 190px;

        @media (max-width: 600px) {
            flex: 1 0 200px;
            width: 100%;
        }
        img,
        picture {
            width: 100%;
            max-height: 100%;
            object-fit: cover;
        }
    }
    &__data {
        flex: auto;
        // width: 100%;
    }
    &__header {
        font-size: 20px;
        line-height: 1.5;
        font-weight: 600;
        display: flex;
        justify-content: space-between;
        gap: 10px 20px;
        margin-bottom: 10px;
        @media (max-width: 600px) {
            flex-direction: column;
        }
        @media (max-width: 320px) {
            margin-bottom: 15px;
        }
    }
    &__title {
        font-weight: 600;
        @media (max-width: 320px) {
            margin-bottom: 10px;
        }
        &:not(:last-child) {
        }
    }
    &__price {
        color: #389ae0;
        white-space: nowrap;
    }
    &__properties {
        margin-bottom: 18px;
    }
    &__property {
        &:not(:last-child) {
            margin-bottom: 5px;
        }
        span {
            font-weight: 600;
            opacity: 0.3;
        }
    }
    &__buttons {
        margin-top: -15px;
        display: flex;
        gap: 20px 30px;
        @media (max-width: 768px) {
            justify-content: space-around;
            gap: 10px;
        }
        @media (max-width: 320px) {
        }
        .button {
            max-width: 200px;
            @include amaxvalue("font-size", 18, 14, 950);
            margin-top: 15px;
            @media (max-width: 320px) {
                padding: 11px;
            }
        }
    }
}
.card--empty {
    .card__title {
        text-align: center;
        width: 100%;
        font-weight: 500;
    }
}
.card--new {
    .card__image {
        position: relative;
        &::after {
            content: "New";
            @include afont(12, 10);
            line-height: math.div(16, 12);
            left: 0px;
            top: -20px;
            position: absolute;
            padding: 2px 10px 2px 5px;
            background: linear-gradient(90deg, #d5e9f7 0%, rgba(213, 233, 247, 0) 100%);
            @media (max-width: 320px) {
                padding: 1px 9px 0 5px;
                top: -15px;
            }
        }
    }
}
