.plan-trip {
    background-color: #fff;
    // padding: 40px 80px;
    @include avalue("padding-top", 40, 14);
    @include avalue("padding-bottom", 40, 14);
    @include avalue("padding-left", 80, 14);
    @include avalue("padding-right", 80, 14);
    position: relative;
    &__header {
    }
    .popup-header {
        position: static;
        .close-button {
            position: absolute;
            top: 0;
            right: 0;
            transform: translate(-50%, 50%);
        }
    }
    &__header-title {
    }
    &__close-button {
    }
    &__body {
    }
    &__form {
        padding: 0;
    }
    &__form-fields {
        label {
            margin-bottom: 0;
        }
    }

    &__goal-select {
    }
    &__multiple-inputs {
        border: none;
        &:focus,
        &:focus-within {
            outline: none;
            input {
                outline: none;
            }
        }
        .form-field-wrapper {
            flex: 0 0 calc(50% - 7.5px);
        }
        input {
            width: 100%;
            &::placeholder {
                color: inherit;
            }
        }
    }

    &__multiple-inputs-body {
        display: flex;
        column-gap: 15px;
        width: 100%;
    }
    &__date-pickers {
    }
    &__visitor-count {
    }
    &__vacation-type-select {
    }
    &__residence-type-select {
    }
    &__service-level-select {
    }
    &__transfer-type-select {
    }
    &__submit-button {
    }
    .just-validate-error-container {
        min-height: auto;
        margin: 0;
    }
    .choices {
        margin: 0;
    }
}
