.page {
    // .page__about-us
    &__about-us {
    }
}
.about-us {
    &__container {
    }
    &__header {
        // display: flex;
        // justify-content: space-between;
        // align-items: center;
        // width: 100%;
    }
    &__body {
        display: flex;
        @media (max-width: 1001px) {
            flex-direction: column;
        }
    }
    // .about-us__text
    &__text {
        @media (min-width: 1001px) {
            max-width: 440px;
        }
        p {
            &:not(:last-child) {
                margin-bottom: 20px;
            }
        }
    }
    // .about-us__title
    &__title {
        // margin-top: 70px;
        // margin-bottom: 40px;
    }
    // .about-us__creators
    &__creators {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        flex-grow: 1;
        position: relative;
        picture {
            align-self: center;
        }
        @media (max-width: 390px) {
            width: calc(100% + 30px);
            margin-left: -15px;
        }
    }
    &__creators-image {
        // height: 550px;
        // width: 440px;
        max-width: 100%;
        object-fit: cover;
        position: absolute;
        left: 0;
        bottom: -50px;
        @media (max-width: 1000px) {
            position: static;
            align-self: center;
        }
    }
    // .about-us__know-more
    &__know-more {
    }
    // .about-us__quote
    &__quote {
        flex: 0 0 auto;
        background: #d5e9f7;
        box-shadow: 0px 0px 25px rgba(12, 92, 112, 0.05);
        @include avalue("padding-top", 30, 20);
        padding-bottom: 20px;
        @include avalue("padding-left", 30, 20);
        @include avalue("padding-right", 30, 20);
        width: 449px;
        z-index: 10;
        position: absolute;
        bottom: 50px;
        right: 0;
        border-radius: 10px;
        @include afont(16, 14);
        @media (max-width: 1000px) {
            position: relative;
            bottom: 0;
            width: auto;
            margin-top: 0;
            @include avalue("margin-left", 25, 0);
            @include avalue("margin-right", 25, 0);
            @include avalue("margin-bottom", 100, 0);
        }
        @media (max-width: 390px) {
            margin-left: 15px;
            margin-right: 15px;
        }
        &::before {
            content: "\e90a";
            font-family: icons;
            font-size: 42px;
            position: absolute;
            top: -33px;
            @include avalue("left", 42, 10);
        }
    }
    &__creators-names {
        font-weight: 600;
        text-align: right;
        display: block;
        margin-top: 10px;
        @media (max-width: 375px) {
            display: none;
        }
    }
}
