.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    font-weight: 600;
    z-index: 50;
    &__container {
    }
    // .header__upper
    &__upper {
    }
    // .header__main
    &__main {
        @media (max-width: 768px) {
            // display: none;
        }
    }
}

.main-header {
    color: #fff;
    //  transform: translateX(-50%);
    //  position: fixed;
    //  top: 90px;
    //  left: 50%;
    width: 100%;
    z-index: 30;
    &__container {
        // height: 90px;
        padding: 22px 20px;
        // @include transition;
        transition: color 0.4s, background-color 0.4s, top 0.4s;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width: 740px) {
            @include avalue("padding-left", 143, 14);
            @include avalue("padding-right", 70, 14);
        }
    }
    &__burger-container {
        display: flex;
        align-items: center;
        @media (max-width: 1070px) {
            // display: none;
        }
    }

    // .main-header__nav
    &__nav {
        margin-left: 25px;
        @media (max-width: 1200px) {
            display: none;
        }
    }
}

.main-menu.header-menu {
    // .main-menu__list
    .header-menu__list {
        display: flex;
        justify-content: flex-end;
        row-gap: 10px;
        flex-wrap: wrap;
        opacity: 0;
        transition: opacity 0.3s;
    }
    // .main-menu__item
    .header-menu__item {
        overflow: hidden;
        white-space: nowrap;
        margin-left: 14px;
        margin-right: 15px;
    }
    // .main-menu__link
    .header-menu__link {
        opacity: 0.8;
        position: relative;
        display: block;
        // margin-bottom: 5px;
        @include transition(opacity);
        &::after {
            content: "";
            @include transition(transform);
            width: 60px;
            height: 4px;
            position: absolute;
            left: -60px;
            bottom: -5px;
            background-color: #fff;
        }
        @media (any-hover: hover) {
            &:hover {
                opacity: 1;
                &::after {
                    transform: translateX(100%);
                }
            }
        }
    }
}
.main-header--sticky {
    background-color: #fff;
    color: #07273e;
    @include fon(14, 21);
    box-shadow: 0px 1px 7px rgba(0, 0, 0, 0.3);
    .main-header__container {
        min-height: 90px;
        animation: fadeInOut 0.4s;
        @keyframes fadeInOut {
            0% {
                opacity: 0;
            }
            80% {
                opacity: 0.5;
            }
            100% {
                opacity: 1;
            }
        }
    }

    .main-header__burger-container {
        align-self: flex-start;
        @media (max-width: 740px) {
            justify-content: space-between;
            width: 100%;
        }
    }
    .main-header__burger-button {
        opacity: 0.5;
        white-space: nowrap;
        transition: opacity 0.3s;
        cursor: pointer;
        @media (max-width: 1260px) {
            margin-top: 0;
        }
        @media (max-width: 740px) {
            display: none;
        }
        @media (any-hover: hover) {
            &:hover {
                opacity: 1;
            }
        }
    }

    .main-menu.header-menu {
        .header-menu__list {
            row-gap: 0;
        }
        .header-menu__item {
            margin-left: 10px;
            margin-right: 10px;
        }
        .header-menu__link {
            opacity: 0.5;
            @media (max-width: 1260px) {
                padding: 10px;
                //  margin-top: 10px;
            }
            @media (any-hover: hover) {
                &:hover {
                    opacity: 1;
                }
            }
        }
    }
}
.header-burger-button {
    opacity: 0.8;
    font-weight: 600;
    display: flex;
    align-items: center;
    font-size: inherit;
    line-height: inherit;
    height: 24px;
    white-space: nowrap;
    @media (max-width: 1070px) {
        display: none;
    }
    .burger-lines {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 34px;
        height: 21px;
        span {
            height: 1px;
            opacity: 1;
            width: 34px;
            background-color: #fff;
            &:nth-child(2) {
                width: 26px;
            }
            &:nth-child(3) {
                width: 30px;
            }
        }
    }
    .burger-text {
        margin-left: 15px;
    }
}
.upper-header {
    width: 100%;
    color: $upperHeaderColor;
    background-color: $upperHeaderBg;
    .header__container {
        min-height: 90px;
        padding: 0 24px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        gap: 30px;
        @media (max-width: 768px) {
            gap: 0;
        }
    }
    .header__wrapper {
        box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.35);
    }
    // .upper-header__logo
    &__logo {
        width: 205px;
        height: 58px;
        margin-right: auto;
        padding: 0 10px;
        @media (max-width: 600px) {
            padding-left: 0;
        }
    }
    &.scrolled &__logo {
        margin-right: 0;
    }
    .main-header__burger-container {
        margin-right: auto;
        margin-left: auto;
    }
    .header-burger-button {
        .burger-lines {
            @media (max-width: 740px) {
                align-items: flex-end;
            }
            span {
                background-color: #07273e;
            }
        }
    }

    &__burger-button {
        display: none;
        .burger-lines {
            align-items: flex-end;
            span {
                background-color: #07273e;
            }
        }
        .burger-text {
            @media (max-width: 1000px) {
                display: none;
            }
        }
        @media (max-width: 1070px) {
            height: 28px;
            display: flex;
        }
        @media (max-width: 768px) {
            margin-left: 40px;
        }
        @media (max-width: 600px) {
            margin-left: 30px;
        }
    }
    // .upper-header__nav
    &__nav {
        opacity: 0.6;
        margin-right: auto;
        margin-left: auto;
    }
    &__buttons {
        display: flex;
        align-items: center;
        gap: 30px;
        @media (max-width: 768px) {
            gap: 40px;
        }
        @media (max-width: 600px) {
            display: none;
        }
    }
    &__button {
        font-size: 28px;
        display: block;
        @media (max-width: 900px) {
            font-size: 25px;
            margin-right: 0;
        }
    }
    // .upper-header__search-button
    &__search-button {
    }
    // .upper-header__language-selector
    &__language-selector {
        text-transform: uppercase;
        font-weight: 400;
        opacity: 1;
        font-size: 16px;
        @media (min-width: 900.02px) {
            margin-right: 19px;
        }
        span:before {
            display: inline;
            margin-left: 5px;
            font-size: 10px;
            vertical-align: middle;
        }
    }
    // .upper-header__phone
    &__phone {
        font-weight: 600;
        white-space: nowrap;
        @media (max-width: 1200px) {
            display: none;
        }
    }
    // .upper-header__phone-back
    &__phone-back {
        @media (max-width: 1200px) {
            display: none;
        }
    }
}

.upper-menu.header-menu {
    // .upper-menu__list
    @media (max-width: 1170px) {
        display: none;
    }
    .header-menu__list {
        display: flex;
        gap: 18px;
    }
    // .upper-menu__item
    .header-menu__item {
    }
    // .upper-menu__link
    .header-menu__link {
        @include transition();
        opacity: 0.75;
        @media (any-hover: hover) {
            &:hover {
                opacity: 1;
            }
        }
    }
}
