.page {
    // .page__why-us
    &__why-us {
    }
}
.why-us {
    &__container {
        padding: 0;
        @media (max-width: 375px) {
            padding-left: 14px;
            padding-right: 14px;
        }
    }
    // .why-us__title
    &__title {
        // margin-top: 100px;
        // margin-bottom: 40px;
    }
    // .why-us__list
    &__list {
        flex-wrap: wrap;
        margin: -30px -20px 0;
        @media (max-width: 768px) {
            margin: 0;
        }
    }
    .slider-controls {
        display: none;
    }
    @media (max-width: 375px) {
        .slider-controls {
            display: block;
            margin-top: 20px;
        }
        &__list {
            margin: 0;
        }
        &__list-container {
            @media (max-width: 375px) {
                margin-right: -14px;
                overflow: hidden;
            }
        }
        &__list-item {
            flex: 0 0 190px;
            // flex: 0 0 100%;
            padding: 14px 0;
        }
        &__text {
            padding: 10px;
        }
    }
}
