.slide {
    background: #d5e9f7;
    //  max-width: 620px;
    padding: 30px;
    box-shadow: 0px 0px 25px rgba(255, 255, 255, 0.05);
    border-radius: 5px;
    @media (max-width: 360px) {
        border-radius: 0;
        box-shadow: none;
        padding: 20px 15px;
    }
    // .slide__header
    &__header {
        margin-bottom: 17px;
        display: flex;
        .slide__reviewer-img {
            width: 80px;
            height: 80px;
        }
    }
    &__header-data {
        display: flex;
        flex-wrap: wrap;
        padding-left: 10px;
        width: 100%;
        @media (min-width: 801px) {
            padding-left: 0;
            // max-height: 110px;
            flex-wrap: nowrap;
            justify-content: space-between;
        }
    }

    &__review-rating-and-date {
        display: flex;
        align-items: center;
        flex: 0 0 auto;
        gap: 5px 20px;
        @media (max-width: 800px) {
            flex: 0 0 100%;
            justify-content: space-between;
            flex-wrap: wrap;
        }
    }
    &__rating {
        //     // background-image: url("data:image/svg+xml;base64,");
        background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="23" height="18"><image width="18" height="18" xlink:href="data:image/svg+xml;base64," /></svg>');
        background-repeat: repeat-x;
        width: 110px;
        height: 18px;
        display: flex;
        position: relative;
        overflow: hidden;
        &::after {
            content: "";
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="23" height="18"><image width="18" height="18" xlink:href="data:image/svg+xml;base64," /></svg>');
        }
        @media (max-width: 800px) {
            margin-left: 0px;
            width: 85px;
            height: 13px;
            margin-right: 0;
            background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="18" height="13"><image width="13" height="13" xlink:href="data:image/svg+xml;base64," /></svg>');
            &::after {
                background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="18" height="13"><image width="13" height="13" xlink:href="data:image/svg+xml;base64," /></svg>');
            }
        }
    }
    &__rating-5-stars {
        &::after {
            left: 0;
        }
    }
    &__rating-4-stars {
        &::after {
            left: -23px;
        }
    }
    &__rating-3-stars {
        &::after {
            left: -23px * 2;
        }
    }
    &__rating-2-stars {
        &::after {
            left: -23px * 3;
        }
    }
    &__rating-1-stars {
        &::after {
            left: -23px * 4;
        }
    }
    @media (max-width: 800px) {
        &__rating-4-stars {
            &::after {
                left: -18px;
            }
        }
        &__rating-3-stars {
            &::after {
                left: -18px * 2;
            }
        }
        &__rating-2-stars {
            &::after {
                left: -18px * 3;
            }
        }
        &__rating-1-stars {
            &::after {
                left: -18px * 4;
            }
        }
    }
    // .slide__rating-date
    &__rating-date {
        opacity: 0.5;
        flex: 1 0 auto;
        @media (max-width: 800px) {
            flex: none;
        }
    }
    // .slide__rating-link
    &__rating-link {
        @include afont(14, 12);
        @media (min-width: 801px) {
            text-align: right;
        }
        @media (max-width: 800px) {
            flex: 0 0 100%;
        }
    }
    // .slide__body
    &__body {
        display: flex;
    }
    // .slide__reviewer
    &__reviewer {
        margin-right: 25px;
        @media (max-width: 800px) {
            display: none;
        }
    }
    &__reviewer-img {
        width: 110px;
        height: 110px;
        flex-shrink: 0;
        img,
        picture {
            height: 100%;
            width: 100%;
        }
    }
    // .slide__reviewer-name
    &__reviewer-name {
        @include afont(14, 12);
        font-weight: 600;
        line-height: math.div(18, 12);
        text-align: center;
        &:not(:first-child) {
            margin-top: 8px;
        }
        @media (max-width: 800px) {
            text-align: left;
            &:not(:first-child) {
                margin-top: 3px;
            }
        }
    }
    // .slide__review
    &__review {
        display: flex;
        flex-direction: column;
        @include fon(16, 24);
    }
    &__review-text {
        font-size: 16px;
        @media (max-width: 800px) {
            font-size: 14px;
        }
    }
    // .slide__review-link
    &__review-link {
        font-weight: 600;
        margin-top: 18px;
        align-self: flex-end;
        border-bottom: 1px solid transparent;
        @include afont(16, 12);
        @media (any-hover: hover) {
            &:hover {
                // text-decoration: underline 
                // causes a small leftward shift of the link
                border-color: inherit;
            }
        }
    }
}
