.wur {
    position: relative;
    .bg {
        transform: translate(-50%, -30%);
        width: auto;
        max-height: 70%;
        max-width: 100%;
        object-fit: contain;
        opacity: 0.6;
        @media (max-width: 1280px) {
            max-height: 50%;
        }
        @media (max-width: 600px) {
            opacity: 0.3;
        }
        @media (max-width: 480px) {
            opacity: 0.15;
        }
    }
    &__container {
        @include amaxvalue("padding-left", 24, 14, 768);
        @include amaxvalue("padding-right", 24, 14, 768);
        position: relative;
        z-index: 10;
    }

    &__header {
        @media (max-width: 375px) {
            margin-bottom: 0;
        }
    }
    &__title {
    }
    &__body {
    }
    &__reason {
        // margin-top: 60px;
        @include avalue("margin-top", 60, 25);
    }
    &__reason-list {
        align-items: flex-start;
        flex-wrap: wrap;
        // margin: -60px 0 0;
        margin: 0;
    }
    &__reason-list-item {
        // flex-basis: calc(50% - 60px * 2);
        display: flex;
        flex: 0 0 50%;
        margin: 0;
        @include aminvalue("padding-left", 60, 0, 768);
        @include aminvalue("padding-right", 60, 0, 768);
        &:nth-child(1) {
            justify-content: flex-end;
        }
        &:nth-child(4) {
            justify-content: flex-end;
        }
        &:nth-child(5) {
            justify-content: flex-end;
        }
        &:nth-child(odd) {
            // padding-right: 121px;
        }
    }
    .slider-controls {
        display: none;
    }
    @media (max-width: 375px) {
        &__reason-list-container {
            margin-right: -14px;
            padding-left: 14px;
            margin-bottom: 30px;
        }
        .slider-controls {
            display: block;
        }
        &__reason-list {
            margin-top: 0;
            margin-left: -14px;
        }
        &__reason-list-item {
            align-self: flex-start;
            width: 190px;
            flex: 0 0 190px;
            margin-right: 14px;
        }
    }
}
.reason {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 270px;
    align-self: flex-end;
    &__number {
        border: 1px solid #389ae0;
        border-radius: 50%;
        background-color: #fff;
        font-weight: 700;
        @include afont(50, 20);
        line-height: math.div(60, 50);
        margin-bottom: 13px;
        // width: 88px;
        // height: 88px;
        @include avalue("width", 88, 40);
        @include avalue("height", 88, 40);
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__title {
        @include fon(20, 30);
        font-weight: 600;
        margin-bottom: 9px;
    }
    &__text {
        text-align: center;
    }
}
