.filter {
    background-color: #fff;
    border-radius: 5px;
    padding: 20px 20px 30px;

    &__header {
        justify-content: center;
    }
    &__title {
        font-weight: 600;
        text-transform: uppercase;
        font-size: 20px;
        line-height: 1.5;
        opacity: 0.5;
        margin-bottom: 20px;
        text-align: center;
    }
    &__section {
        &:not(:last-child) {
            margin-bottom: 40px;
        }
    }
    &__status {
    }
    &__section-title {
        @include afont(20, 14, 320);
        line-height: 1.5;
        font-weight: 600;
        margin-bottom: 12px;
    }
    &__list {
    }
    &__list-item {
        display: flex;
        cursor: pointer;
        padding: 3px 5px 0;
        border-radius: 2px;
        input[type="checkbox"] {
            margin-right: 10px;
        }
        @media (any-hover: hover) {
            &:hover {
                input[type="checkbox"] {
                    &:before {
                        outline: 1px solid;
                    }
                }
            }
        }
        &:focus:not(.using-mouse),
        &:focus-within:not(.using-mouse) {
            outline: 1px solid #828282;
        }
        &:not(:last-child) {
            margin-bottom: 12px;
        }
        label {
            font-weight: inherit;
            width: 100%;
            cursor: inherit;
        }
    }
    &__type {
    }
    &__price-range-input {
        display: flex;
        align-items: baseline;
        label,
        span {
            opacity: 0.5;
        }
    }
    &__price {
    }
    &__price-input {
        width: 150px;
        height: 40px;
        padding: 13px 14px;
        margin-right: 10px;
        margin-left: 10px;
        margin-bottom: 20px;
        color: #07273e;
        text-align: right;
        border: 1px solid #ccc;
        &::placeholder {
            color: inherit;
            opacity: 1;
        }
        &:focus,
        &:focus-within {
            outline: 1px solid #828282;
        }
    }
    &__price-range {
    }
    &__location {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 35px;
        height: auto;
    }
    .selected-option {
        // border: 1px solid;
    }
    .choices {
        border: 1px solid #828282;
    }
    &__submit-button {
        max-width: 100%;
        width: 100%;
    }
    .close-button {
        display: none;
    }
}
.popup--open {
    .filter__close-button {
        display: block;
    }
    .popular__filter {
        margin-right: 0;
    }
}
