.btn {
    display: inline-flex;
    padding: 0 20px;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease 0s;
    @media (min-width: $md2 + px) {
        @media (any-hover: hover) {
            &:hover {
                background-color: inherit;
            }
        }
    }
    &._fw {
        width: 100%;
    }
}

.select {
    position: relative;
    &__item {
        position: relative;
    }
}
