.floating-nav {
    background: #fff;
    box-shadow: 1px 1px 7px rgb(0 0 0 / 20%);
    height: 50px;
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 40;
    opacity: 0;
    transition: opacity 0.3s;
    pointer-events: none;
    border-radius: 2px 0 0 0;
    @media (max-width: 768px) {
        border-radius: 0;
    }
    @media (max-width: 375px) {
        width: 100%;
    }
    &--open {
        opacity: 1;
        pointer-events: all;
    }
    // .floating-nav__list
    &__list {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 3px 0;
    }
    // .floating-nav__link
    &__link {
        font-size: 0;
        line-height: 0;
        margin: 0;
        padding: 0;
        &::before {
            font-size: 29px;
            line-height: 44px;
            cursor: pointer;
        }
    }
    // .floating-nav__link-visible
    &__link-visible {
        @include fon(14, 21);
        text-align: center;
        height: 44px;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        &::before {
            content: "\e90d";
            font-family: icons;
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            height: 11px;
            width: 11px;
            color: #ff831e;
        }
        &::after {
            content: "";
            position: absolute;
            top: 15px;
            left: 52%;
            transform: translateX(-50%);
            height: 12px;
            width: 1px;
            background: linear-gradient(transparent, #ff831e, transparent);
        }
    }
    // .floating-nav__list-item
    &__list-item {
        width: 80px;
        display: flex;
        justify-content: center;
        align-items: center;
        &:not(:last-child) {
            border-image: linear-gradient(transparent, #ff831e, transparent) 1;
            border-width: 0 1px 0 0;
            border-style: solid;
        }
        @media (max-width: 375px) {
            width: auto;
            flex: 0 0 24%;
        }
    }
}
