.footer {
    margin-top: 10px;
    color: #fff;
    background-color: #412f2f;
    background-size: cover;
    padding-bottom: 40px; // for floating nav
    position: relative;
    @media (max-width: 768px) {
        background: repeat-y center url(@img/footer/footer-bg-line.jpg);
    }
    .bg {
        z-index: 1;
    }
    &__container {
        position: relative;
        z-index: 5;
    }
    // .footer__body
    &__body {
        padding: 70px 20px 25px;
        display: flex;
        justify-content: space-between;
        @media (max-width: 768px) {
            flex-wrap: wrap;
            padding: 0 24px;
        }
        @media (max-width: 680px) {
            flex-direction: column;
        }
        @media (max-width: 480px) {
            padding: 0;
        }
    }
    &__logo-container {
        @include flex-center;
        @media (max-width: 768px) {
            flex: 0 0 100%;
        }
        img {
            max-width: 100%;
        }
    }
    // .footer__logo
    &__logo {
        @media (min-width: 769px) {
            width: 221px;
            height: 224px;
        }
        @media (max-width: 768px) {
            margin: 50px auto;
        }
    }
    ._footer-column-list {
        @media (max-width: 680px) {
            padding: 10px 0;
        }
    }
    ._column-title {
        @media (max-width: 680px) {
            margin-bottom: 0;
            padding: 10px 0;
        }
    }
    ._footer-column {
        @media (max-width: 680px) {
            &:not(:nth-last-child(2)) {
                border-bottom: 1px solid rgba(256, 256, 256, 0.3);
            }
        }
    }
    // .footer__cities
    &__cities {
    }
    // .footer__cities-title
    &__cities-title {
    }
    // .footer__cities-list
    &__cities-list {
    }
    // .footer__cities-item
    &__cities-item {
    }
    // .footer__cities-link
    &__cities-link {
    }
    // .footer__services
    &__services {
    }
    // .footer__services-title
    &__services-title {
    }
    // .footer__services-list
    &__services-list {
    }
    // .footer__services-list-item
    &__services-list-item {
    }
    // .footer__services-link
    &__services-link {
    }
    // .footer__about-us
    &__about-us {
    }
    // .footer__about-us-title
    &__about-us-title {
    }
    // .footer__about-us-list
    &__about-us-list {
    }
    // .footer__about-us-list-item
    &__about-us-list-item {
    }
    // .footer__about-us-link
    &__about-us-link {
    }
    &__go-up-container {
        display: flex;
        align-items: center;
        @media (max-width: 768px) {
            align-self: flex-end;
        }
        @media (max-width: 680px) {
            display: none;
        }
    }
    // .footer__go-up
    &__go-up {
        font-weight: 600;
        position: relative;
        height: 188px;
        padding-bottom: 5px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        cursor: pointer;
        &::before {
            content: "\e90d";
            font-family: icons;
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 40px;
            height: 40px;
            font-size: 40px;
        }
        &::after {
            content: "";
            position: absolute;
            top: 22px;
            left: 50%;
            height: 146px;
            width: 2px;
            transform: translateX(-50%);
            background: linear-gradient(transparent, #fff, transparent);
        }
    }
    // .footer__lower-footer
    &__lower-footer {
        margin: 42px 16px 0px;
    }
}

.lower-footer {
    @include avalue("padding-top", 45, 21);
    @include avalue("padding-bottom", 45, 21);
    @include avalue("padding-left", 48, 0);
    @include avalue("padding-right", 48, 0);
    text-align: center;
    border: 1px solid;
    border-width: 1px 0 0 0;
    // .lower-footer__text
    &__text {
        @include afont(16, 12);
        font-weight: 500;
        @media (max-width: 680px) {
            font-weight: 400;
        }
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
}
._column-title {
    @include fon(20, 30, 600);
    margin-bottom: 12px;
}
.footer__list-item {
    @include transition;
    &:not(:last-child) {
        margin-bottom: 10px;
        @media (max-width: 680px) {
            margin-bottom: 5px;
        }
    }
    @media (any-hover: hover) {
        &:hover {
            color: #ff831e;
        }
    }
}
