.burger {
    color: #07273e;
    @include fon(16, 20, 500);
    background-color: #fff;
    @include avalue("padding-left", 200, 24);
    @include avalue("padding-right", 200, 24);
    @include avalue("padding-top", 25, 15);
    padding-bottom: 0;
    @media (max-width: 768px) {
        padding-left: 50px;
        padding-right: 50px;
    }
    @media (max-width: 480px) {
        padding-right: 20px;
        padding-left: 20px;
    }

    &--open {
        // display: block;
        // opacity: 1;
        // animation: fadeIn 0.5s;
    }
    &__container {
        height: auto;
    }
    &__inner {
        max-width: 1082px;
        @media (max-width: 1000px) {
            padding: 24px 24px 0 80px;
        }
        @media (max-width: 800px) {
            padding: 24px 24px 0;
        }
        &.popup-inner {
            width: 98%;
        }
    }
    &__header {
        // color: #333;
        opacity: 0.6;
        margin-bottom: 40px;
    }
    &__selected-main-item {
        margin-right: 20px;
    }
    &__controls {
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
    &__close-button {
    }
    &__menu-body {
        display: flex;
        margin-top: 5px;
    }
    // .burger__main-list
    &__main-list {
    }
    &__list-container {
        max-width: 250px;
        @media (max-width: 768px) {
            width: 100%;
            max-width: 100%;
        }
    }
    .burger-list-item {
        overflow: hidden;
        padding: 10px;
        border-radius: 4px;
        &:not(.using-mouse):focus-within {
            @media (min-width: 769px) {
                outline: 1px solid;
            }
        }
    }
    // .burger__main-list-item
    &__main-list-item {
        &:not(:last-child) {
            margin-bottom: 14px;
        }
    }
    // .burger__main-link
    .burger-link {
        opacity: 0.6;
        position: relative;
        transition: opacity 0.3s;
        display: block;
        @media (any-hover: hover) {
            &:hover {
                opacity: 1;
            }
        }

        &::before {
            transition: width 0.3s;
            content: "";
            width: 0;
            height: 4px;
            background-color: #389ae0;
            position: absolute;
            bottom: -6px;
            left: 0;
        }
        &--active {
            opacity: 1;
            &::before {
                width: 60px;
            }
        }
    }
    &__main-link {
        @include fon(20, 24, 600);
        position: relative;
    }
    &__secondary-list {
        @media (min-width: 769px) {
            margin-left: 100px;
            min-width: 250px;
            display: none;
        }
        @media (max-width: 768px) {
            margin-top: 25px;
        }
        &--open {
        }
        .burger-link--active {
            &::before {
                display: none;
            }
        }
    }
    .burger-link-list--open {
        display: block;
    }
    // .burger__secondary-list-item
    &__secondary-list-item {
        &:not(:last-child) {
            margin-bottom: 13px;
        }
        &:not(.using-mouse):focus-within {
            @media (any-hover: hover) {
                outline: 1px solid #828282;
            }
        }
    }
    // .burger__secondary-link
    &__secondary-link {
    }
    // .burger__secondary-list-container
    &__secondary-list-container {
        @media (max-width: 768px) {
            display: none;
        }
    }
    &__footer {
        // height: 143px;
        border-top: 1px solid #ccd4d6;
        margin-top: 33px;
        @extend %center;
        &-contacts {
            width: 100%;
            padding-left: 0;
            padding-right: 0;
            @include amaxvalue("padding-top", 30, 5, 600);
            @include amaxvalue("padding-bottom", 30, 5, 600);
            // @include avalue2("margin-top", -)

            .contacts {
                @include amaxvalue("margin-top", -20, 5, 600);
                // @include amaxvalue("padding-top", 20, 0, 600);
                justify-content: space-between;
                @media (max-width: 900px) {
                    justify-content: space-around;
                    flex-wrap: wrap;
                }
                @media (max-width: 375px) {
                    // flex-direction: column;
                }
            }
            .contacts-item {
                border: none;
                @include amaxvalue("padding", 15, 5, 600);
                @include amaxvalue("margin-top", 20, 5, 600);
                &:last-child {
                    padding-right: 0;
                }
                &:first-child {
                    padding-left: 0;
                }
                @media (max-width: 900px) {
                    padding: 20px !important;
                }
                @media (max-width: 375px) {
                    width: 100%;
                    &:not(:last-child) {
                        border-bottom: 1px solid #ccd4d6;
                    }
                }
            }
            .contacts-link-block {
                @media (max-width: 375px) {
                    justify-content: center;
                    li:not(:last-child) {
                        margin-right: 20px;
                    }
                }
            }
            .contacts-text {
                @include fon(16, 24);
            }
            .contacts__call-link {
                @include fon(16, 24);
            }
        }
    }

    ._spoiler--init [data-spoiler]::after {
        color: inherit;
    }
    ._spoiler--init [data-spoiler]._spoiler--open::after {
        color: inherit;
    }
}
.popup--open {
    .burger {
        display: block;
        &__secondary-list {
            @media (max-width: 768px) {
                // margin-left: 0;
            }
        }
        &__secondary-link {
            display: inline;
        }
    }
}
