@use "sass:math";
@import "common/mixins";
@import "reset";

$fontFamily: Montserrat;
$background: rgba(253, 253, 253, 0.2);
$mainTextColor: #07273e;

$upperHeaderColor: $mainTextColor;
$upperHeaderBg: #fdfdfd;
$mainHeaderColor: #fff;

$minWidth: 320;
$maxWidth: 1920; // 1920;
$maxWidthContainer: 1280; // 1170;
$md1: $maxWidthContainer + 12;
$md2: 991.98;
$md3: 787.98;
$md4: 479.98;

html {
    scroll-behavior: smooth;
}
body {
    font-family: $fontFamily;
    line-height: math.div(24, 16);
    @include afont(16, 14);
    color: $mainTextColor;
    background-color: $background;
    &._lock {
        overflow: hidden;
    }
}

.wrapper {
    width: 100%;
    min-height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    &._loaded {
    }
}
._container {
    max-width: $maxWidthContainer + 15 + px;
    margin: 0 auto;
    padding: 0 15px;
    // @media (max-width: $md1 + px) {
    //     max-width: 970px;
    // }
    // @media (max-width: $md2 + px) {
    //     max-width: 750px;
    // }
    // @media (max-width: $md3 + px) {
    //     max-width: none;
    //     padding: 0 10px;
    // }
}
.page {
    flex: 1 1 auto;
    flex-direction: column;
}
.popups {
    display: none;
}
