._spoiler--init {
    [data-spoiler] {
        position: relative;
        padding-right: 44px;
        &::after {
            content: "\e902";
            font-family: icons;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            transition: transform 0.2s;
            right: 10px;
            font-weight: 100;
            font-size: 15px;
        }
        &._spoiler--open {
            &::after {
                transform: translateY(-50%) rotateX(180deg);
            }
        }
    }
}
