button {
    border: none;
    background-color: transparent;
    cursor: pointer;
    color: inherit;
}

a {
    color: inherit;
    text-underline-offset: 0.1875em;
}
._section {
    @include avalue("padding-top", 50, 30);
    @include avalue("padding-bottom", 50, 30);
}
._section-header {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    // @include avalue("margin-top", 100, 60);
    @include avalue("margin-bottom", 40, 25);
    gap: 35px;
}

._section-header-center {
    justify-content: center;
}
._section-title {
    line-height: math.div(61, 50);
    @include afont(50, 20);
    font-weight: 700;
    text-align: center;
}
._section-header-with-subtitle {
    display: block;
    ._section-title {
        margin-bottom: 15px;
    }
}
._section-title-link {
    @include afont(20, 12);
    line-height: math.div(24, 20);
    font-weight: 600;
    text-align: right;
    text-decoration: underline;
}
.button {
    max-width: 260px;
    height: 50px;
    padding-left: 20px;
    padding-right: 20px;
    @include avalue("padding-top", 14, 10);
    @include avalue("padding-bottom", 14, 10);
    @include fon(18, 22, 500);
    text-align: center;
    border-radius: 5px;
    white-space: nowrap;
    @media (max-width: 768px) {
        // @include avalue("width", 200, 145);
        @include avalue("height", 50, 40);
        @include afont(18, 14);
    }
    @media (any-hover: hover) {
        &:focus:not(.using-mouse) {
            outline: 3px solid #ff831e;
            outline-offset: 3px;
        }
    }
}
.dark-button {
    background-color: #ff831e !important;
    color: #fff;
    transition: box-shadow 0.3s;
    @media (any-hover: hover) {
        &:hover {
            background-color: #f47f1f;
            box-shadow: 0px 0px 27px rgba(0, 0, 0, 0.1);
        }
        &:active {
            background-color: #eb7717;
            box-shadow: inset 0px 0px 15px #f87a13;
        }
        &:focus:not(.using-mouse) {
            outline: 3px solid #ff831e;
            outline-offset: 3px;
        }
    }
    &:disabled {
        background-color: #e6d0be;
    }
}
.light-button {
    background-color: #fff;
    color: #ff831e;
    border: 1px solid;
    @media (any-hover: hover) {
        &:hover {
            background-color: rgba(252, 129, 30, 0.1);
            color: #ff8f32;
        }
        &:active {
            background-color: #eb7717;
            color: #fff;
            box-shadow: inset 0px 0px 15px #f87a13;
        }
    }
}
.choices {
    border-radius: 4px;
    flex-shrink: 0;
    height: 100%;
    &__placeholder {
        font-style: italic;
    }
    &__inner {
        border: none !important;
        background-color: #fff !important;
        font-size: inherit !important;
        border-color: inherit !important;
        position: relative;
        color: #828282;
    }
    &__list--dropdown,
    &__list[aria-expanded] {
        z-index: 50 !important;
        background-color: #fff !important;
        word-break: break-word;
        outline: 2px solid;
        color: #828282;
        border: none !important;
        margin-top: 4px !important;
        border-radius: 2.5px !important;
        .choices__item--selectable {
            &.is-selected {
                background-color: #389ae0;
                color: #fff;
            }
            &.is-highlighted {
                background-color: #d5e9f7 !important;
                color: inherit;
            }
        }
    }
    &.is-flipped {
        .choices__list--dropdown,
        .choices__list[aria-expanded] {
            margin-bottom: 4px !important;
        }
    }
}
@keyframes fadeOut {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
        // display: none;
    }
}
.contrast-bg {
    background-color: rgba(255, 255, 255, 0.8);
    padding: 0.14em 0;
}
.swiper {
    position: relative;
    &::after {
        //   content: "";
        position: absolute;
        top: 0;
        right: 0;
        width: 100px;
        height: 100%;
        background: linear-gradient(
            90deg,
            rgba(255, 255, 255, 0.1) 0,
            rgba(255, 255, 255, 0.4) 20%,
            rgba(255, 255, 255, 0.8) 50%,
            #fff 100%
        );
    }
}
.swiper-wrapper {
    display: flex;
}
.swiper-initialized {
    .swiper-wrapper {
        flex-wrap: nowrap;
    }
}
br.responsive {
    display: none;
}
form {
    &:focus-within:not(.using-mouse) {
        .range-slider {
            outline: 1px solid #828282;
        }
    }
}
.range-slider {
    &:focus-within:not(.using-mouse) {
    }
    &.noUi-target {
        display: block;
        width: 100%;
        border: none;
        box-shadow: none;
        background: none;
        height: 24px;
        padding: 0 16px;
    }
    .noUi-base {
        height: 100%;
        width: 100%;
        //   margin: 6px 0;
        position: relative;
        margin: 0;
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        &::before {
            content: "";
            height: 4px;
            background-color: rgba(130, 130, 130, 0.3);
            position: absolute;
            width: 100%;
            top: 50%;
            transform: translateY(-50%);
        }
    }
    .noUi-connects {
        //   cursor: pointer;
        //   z-index: 10;
    }
    .noUi-connect {
        position: absolute;
        left: 0;
        top: 0;
        background-color: transparent;
        &::before {
            content: "";
            width: 100%;
            height: 4px;
            background: linear-gradient(90deg, #ff831e 0%, rgba(255, 255, 255, 0.75) 120%);
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
        }
    }
    .noUi-handle {
        width: 24px !important;
        height: 24px !important;
        position: absolute;
        background: none;
        top: 0 !important;
        right: -12px !important;
        border: none;
        box-shadow: none;
        //   cursor: pointer;
        &:before,
        &::after {
            display: none;
        }
    }
    .noUi-touch-area {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        background-color: #fff;
        &::before {
            content: "\e90d";
            font-family: icons;
            font-size: 24px;
            line-height: 1;
            position: absolute;
            border: none !important;
            top: 0;
            right: 0;
            color: #ff831e;
            transform: rotate(90deg);
        }
    }
}
.form-field {
    @include afont(16, 14);
    line-height: math.div(22, 16);
    padding: 14px 15px;
    border-radius: 4px;
    display: flex;
    gap: 0.5em;
}
.plan-trip {
    .form-field {
        padding: 0;
    }
}
.form-placeholder {
    flex-shrink: 0;
    color: #828282;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    display: none;
}
input[type="checkbox"] {
    cursor: pointer;
    &::before {
        transition: outline-color 3s;
    }
    @media (any-hover: hover) {
        &:focus {
            outline: 1px transparent;
            &:before {
                outline: 1px solid;
            }
        }
    }
}
input {
    font-size: inherit;
    border-radius: 4px;
    &::placeholder {
        opacity: 0.5;
        line-height: 22;
    }
}
input,
textarea {
    color: #828282;
    &:-webkit-autofill,
    &:-webkit-autofill:focus {
        transition: background-color 600000s 0s, color 600000s 0s;
    }
}
input[type="checkbox"] {
    @include size(0);
    color: #389ae0;
    flex: 0 0 20px;
    position: relative;
    &::before {
        content: "";
        @include size(20);
        @include abs;
        border-radius: 2px;
        border: 1px solid;
        background-color: transparent;
    }
    &:checked {
        &::after {
            content: "\e90c";
            font-family: icons;
            color: #389ae0;
            @include abs(6, 7);
            @include size(10, 7);
            font-size: 7px;
        }
    }
}

input::placeholder {
    font-style: italic;
}
input.empty,
.form-placeholder {
    font-style: italic;
    opacity: 0.5;
}
.input-wrapper {
    border: 1px solid;
    border-radius: 4px;
    // height: 50px;
    height: 48px; // to accomodate border
    &:focus-within {
        outline: 1px solid;
    }
    .input-container {
        padding: 13px 15px;
        position: relative;
    }
    input {
        border: none;
        height: 24px;
        line-height: 24;
        padding: 13px 15px;
        margin: 0;
        padding: 0;
        background-color: transparent;
        &[type="date"] {
            position: relative;
        }
        &[type="date"]::after {
            // content: "\e901";
            font-family: icons;
            width: 18px;
            height: 18px;
            font-size: 18px;
            color: #389ae0;
            line-height: 1;
            position: absolute;
            right: 20px;
            top: 50%;
            transform: translateY(-50%);
        }
        &:focus-within,
        &:focus {
            outline: none;
        }
    }
    .input-title {
        float: left;
        margin-top: -8px;
        margin-left: 22px;
        padding: 0 1px;
        font-size: 14px;
        line-height: 1;
        color: rgba(7, 39, 62, 0.6);
        background-color: #fff;
        position: relative;
        z-index: 10;
        // anulling .popup label
        padding-top: 0;
        margin-bottom: 0;
        font-weight: 400;
    }
}
.nobr {
    white-space: pre;
}
.card-item {
    &-3 {
        flex: 0 0 33.33333%;
        max-width: 33.33333%;
        @media (max-width: 768px) {
            flex: 0 0 50%;
            max-width: 50%;
        }
    }
    &-4 {
        flex: 0 0 25%;
        max-width: 25%;
        @media (max-width: 800px) {
            flex: 0 0 50%;
            max-width: 50%;
        }
    }
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
    @include avalue("margin-top", 50, 20);

    &-text {
        padding: 0 13%;
        align-self: flex-end;
        text-align: center;
    }
    &-icon {
        .loading {
            @include size(70);
        }
        @include avalue("width", 120, 80);
        @include avalue("height", 120, 80);
        @include flex-center;
        margin-top: 20px;
        @include avalue("margin-bottom", 35, 20);
    }
}
._ibg {
    position: relative;
    img {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        object-fit: cover;
    }
}
body.ie {
    ._ibg {
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }
    ._ibg img {
        width: 0;
        height: 0;
        opacity: 0;
        visibility: hidden;
    }
}

.row:after {
    display: block;
    content: "";
    clear: both;
}
.rub:after {
    content: "\u20BD";
}
%noselect {
    user-select: none;
}
%center {
    display: flex;
    justify-content: center;
    align-items: center;
}
.bg {
    @include abs-center;
    width: 101%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
}
.ui-datepicker {
    display: none;
}
.loader {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
    pointer-events: none;
    div {
        box-sizing: border-box;
        display: block;
        position: absolute;
        width: 80%;
        height: 80%;
        margin: 8px;
        border: 8px solid rgba(#ff831e, .75);
        border-radius: 50%;
        animation: cards-loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
        border-color: #d5e9f7 transparent transparent transparent;
    }
    div:nth-child(1) {
        animation-delay: -0.45s;
    }
    div:nth-child(2) {
        animation-delay: -0.3s;
    }
    div:nth-child(3) {
        animation-delay: -0.15s;
    }
    @keyframes cards-loader {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
}
.swal2-container {
    color: #000;
    .swal2-styled.swal2-confirm {
        @extend .dark-button;
        &:focus {
            box-shadow: none;
        }
    }
    .swal2-icon.swal2-success .swal2-success-ring {
        border-color: rgba(#ff831e, .75);
    }
    .swal2-icon.swal2-success [class^="swal2-success-line"] {
        background-color: rgba(#ff831e, .75);
    }
}
