.questions {
    color: #fff;
    padding-bottom: 17px;
    background-size: cover;
    position: relative;
    background-color: #389ae0;
    .bg {
        z-index: 1;
    }
    // .questions__container
    &__container {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0;
        position: relative;
        z-index: 5;
    }
    &__header {
        @include avalue("margin-top", 50, 24);
    }
    // .questions__title
    &__title {
    }
    // .questions__text
    &__text {
        padding: 0 24px;
        @include afont(25, 16);
        line-height: math.div(33, 25);
        text-align: center;
    }
    // .questions__button
    &__button {
        @include avalue("margin-top", 42, 32);
    }

    &__contacts {
        @extend %center;
        @include avalue("padding-top", 50, 0);
        max-width: 1280px;
        @include avalue("margin-top", 60, 30);
        @include avalue("margin-left", 30, 14);
        @include avalue("margin-right", 30, 14);
        margin-bottom: 0;
        border-top: 1px solid #fff;
        @media (max-width: 700px) {
            height: auto;
            max-width: 80%;
            .contacts {
                flex-direction: column;
                width: 100%;
                .contacts-item {
                    width: 100%;
                    @include avalue("padding-left", 100, 15);
                    @include avalue("padding-right", 100, 15);
                    &:not(:last-child) {
                        border-image: linear-gradient(
                                90deg,
                                rgba(255, 255, 255, 0.06),
                                #fff,
                                rgba(255, 255, 255, 0.06)
                            )
                            1;
                        border-width: 0px 0px 1px 0px;
                    }
                }
                .contacts-link-block {
                    margin: 0 auto;
                    li:not(:last-child) {
                        margin-right: 33px;
                    }
                }
            }
        }
    }
}
