.form-popup {
    background-color: #fff;
    padding: 25px 30px 70px;
    @media (max-width: 768px) {
        padding-bottom: 30px;
    }
    @media (max-width: 430px) {
        padding: 20px;
    }
    .popup-inner {
        @media (max-width: 560px) {
            width: 100%;
        }
    }
    .popup__form-fields {
        #phone {
            letter-spacing: 0.02em;
        }
    }
    .agreement-wrapper {
        .form-input-wrapper {
            cursor: pointer;
            display: flex;
            column-gap: 25px;
        }
        input {
            flex-basis: 0;
        }
        label {
            cursor: inherit;
            font-weight: 400;
            flex-grow: 1;
        }
    }
}
.time-wrapper {
    .input-container {
        display: flex;
        align-items: center;
        gap: 0.5em;
        width: 100%;
        padding: 0;
    }
}
.message-field {
    height: auto;
    .input-container {
        padding: 0;
    }
    #message {
        background-color: inherit;
    }
}
