.page {
    .video {
        padding-top: 0;
    }
}
.video {
    &__container {
        position: relative;
        @include avalue("height", 780, 180);
        overflow: hidden;
    }
    &__video {
        //   max-height: 780px;
        height: 100%;
        object-fit: cover;
        width: 100%;
        cursor: pointer;
    }
    .bg {
        max-height: 780px;
        width: 100%;
    }

    .loaded {
        .loader {
            opacity: 0;
        }
    }
    .loader {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        @include size(400);
        opacity: 0.3;
        transition: opacity 0.3s;
        & div {
            border-width: 40px;
            margin: 40px;
            @include size(320);
            border-top-color: #fff;
        }
        @media (max-width: 1440px) {
            @include size(300);
            & div {
                border-width: 30px;
                @include size(260);
            }
        }
        @media (max-width: 768px) {
            @include size(200);
            & div {
                border-width: 20px;
                margin: 20px;
                @include size(160);
            }
        }
        @media (max-width: 480px) {
            @include size(100);
            & div {
                border-width: 10px;
                margin: 10px;
                @include size(80);
            }
        }
    }
    .video-play-pause-btn {
        &--hidden {
            display: none;
        }
        &::before {
            opacity: 0.75;
            content: "";
            @include abs-center;
            width: 18%;
            height: 35%;
            background: no-repeat 50% / contain
                url('data:image/svg+xml;utf8,<svg width="232" height="269" viewBox="0 0 232 269" fill="none" xmlns="http://www.w3.org/2000/svg"><path opacity="0.6" d="M1.25 2.43113L230 134.5L1.25 266.569L1.25 2.43113Z" fill="white" stroke="white" stroke-width="2"/></svg>');
            cursor: pointer;
            transition: opacity 0.2s;
        }
        @media (any-hover: hover) {
            &:hover {
                &::before {
                    opacity: 1;
                }
            }
        }
    }
}
